.footer {
    /* Light background color */
   padding: 20px;              /* Padding around the content */
   text-align: center;         /* Center align text */
   color:azure;                /* Darker text color for readability */
   clear: both;                /* Ensure the footer clears floated elements */
   font-weight: bold;          /* Use 'bold' for strong emphasis or use a numeric value like 700 */
   font-size: 10px;            /* Font size */
   /* Optional: Add some shadow for visual separation */
   position: relative;
   bottom: 0;
   width: 100%;
 }