body {
  font-family: Arial, sans-serif;
  background-image: url('C:\Users\HP\Desktop\apiProject\customcuisine\public\Image\bowl.jpeg') ;
  background-size: cover; /* Ensure the image covers the entire background */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-attachment: fixed;
}

.app {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 40px 24px;
}

.app__searchForm {
  margin-top: 18px;
}
h1{
  color: aliceblue;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__submit:hover {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: white;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: #02aff0;
  cursor: pointer;
}

.app__recipes {
  margin-top: 24px;
  display: grid;
  grid-template-columns: 400px 400px;
  
 

  margin-top: 24px;
  display: grid;
  
  gap: 20px;
  justify-items: center; /* Center items horizontally within each grid cell */
  align-items: center; /* Center items vertically */
  background-color: rgba(240, 255, 255);

  padding: 20px; /* Added padding to the container */
  box-sizing: border-box; /* Include padding in the container's width */
}

.app__recipes > * {
  border: 1px solid #0c0c0c;
  padding: 20px;
  box-sizing: border-box;
  text-align: center;
}


.app__healthLabels{
   padding: 7px 8px;
   margin-left: 6px;
   border-radius: 4px;
   background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}